export async function customConversationApi(options: any): Promise<Response> {
  const response = await fetch(
    "https://avanade-app-001.azurewebsites.net/api/santander_chatbot/",
    {
      method: "POST",
      //mode: "no-cors",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify(options),
    }
  );

  // console.log(response);
  return response;
}

