import { useRef, useState, useEffect } from "react";
import { Stack } from "@fluentui/react";
import { BroomRegular, DismissRegular, SquareRegular } from "@fluentui/react-icons";

import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import rehypeRaw from "rehype-raw";
import { v4 as uuidv4 } from "uuid";

import styles from "./Chat.module.css";
import Azure from "../../assets/Azure.svg";

import { ChatMessage, Citation, ToolMessageContent, testApi, ChatQuestion, ChatHistory, customConversationApi } from "../../api";
import { Answer } from "../../components/Answer";
import { QuestionInput } from "../../components/QuestionInput";

const Chat = () => {
	const lastQuestionRef = useRef<string>("");
	const chatMessageStreamEnd = useRef<HTMLDivElement | null>(null);
	const [isLoading, setIsLoading] = useState<boolean>(false);
	const [showLoadingMessage, setShowLoadingMessage] = useState<boolean>(false);
	const [activeCitation, setActiveCitation] = useState<[content: string, id: string, title: string, filepath: string, url: string, metadata: string]>();
	const [isCitationPanelOpen, setIsCitationPanelOpen] = useState<boolean>(false);
	const [questions, setQuestions] = useState<string[]>([]);
	const [answers, setAnswers] = useState<string[]>([]);
	const [referencias, setReferencias] = useState<string[]>([]);
	const [isReferenciasPanelOpen, setIsReferenciasPanelOpen] = useState<boolean>(false);
	const [chatHistory, setChatHistory] = useState<ChatHistory[]>([]);

	const abortFuncs = useRef([] as AbortController[]);
	const [conversationId, setConversationId] = useState<string>(uuidv4());

	const makeQuestion = async (question: string) => {
		lastQuestionRef.current = question;

		setIsLoading(true);
		setShowLoadingMessage(true);
		const abortController = new AbortController();
		abortFuncs.current.unshift(abortController);

		const request: ChatQuestion = {
			currentMessage: question,
			lastHistory: questions,
		};

		if (questions.length == 5) questions.shift();

		let res: any;
		try {
			const response = await customConversationApi(request);
			// const response = testApi();
			if (response?.body) {
				const reader = response.body!.getReader();
				while (true) {
					const { done, value } = await reader.read();
					if (done) break;
					res = JSON.parse(new TextDecoder("utf-8").decode(value));
				}

				setQuestions([...questions, question]);
				setChatHistory([...chatHistory, { type: "question", message: question }, { type: "answer", message: res.respuesta }]);
				if (res.referencias_busqueda) setReferencias([...referencias, res.referencias_busqueda]);
			}
		} catch (e) {
			if (!abortController.signal.aborted) {
				alert("An error occurred. Please try again. If the problem persists, please contact the site administrator.");
				console.log(e);
			}
		} finally {
			setIsLoading(false);
			setShowLoadingMessage(false);
			abortFuncs.current = abortFuncs.current.filter((a) => a !== abortController);
		}

		return abortController.abort();
	};

	const clearChat = () => {
		lastQuestionRef.current = "";
		setActiveCitation(undefined);
		setAnswers([]);
		setChatHistory([]);
		setQuestions([]);
		setReferencias([]);
		setConversationId(uuidv4());
	};

	const stopGenerating = () => {
		abortFuncs.current.forEach((a) => a.abort());
		setShowLoadingMessage(false);
		setIsLoading(false);
	};

	useEffect(() => chatMessageStreamEnd.current?.scrollIntoView({ behavior: "smooth" }), [showLoadingMessage]);

	const onShowCitation = (citation: Citation) => {
		setActiveCitation([citation.content, citation.id, citation.title ?? "", citation.filepath ?? "", "", ""]);
		setIsCitationPanelOpen(true);
	};

	const parseCitationFromMessage = (message: ChatMessage) => {
		if (message.role === "tool") {
			try {
				const toolMessage = JSON.parse(message.content) as ToolMessageContent;
				return toolMessage.citations;
			} catch {
				return [];
			}
		}
		return [];
	};

	return (
		<div className={styles.container}>
			<Stack horizontal className={styles.chatRoot}>
				<div className={styles.chatContainer}>
					{!lastQuestionRef.current ? (
						<Stack className={styles.chatEmptyState}>
							<img src={Azure} className={styles.chatIcon} aria-hidden="true" />
							<h1 className={styles.chatEmptyStateTitle}>Comece a conversar...</h1>
							<h2 className={styles.chatEmptyStateSubtitle}>Este assistente está configurado para responder às suas questões</h2>
						</Stack>
					) : (
						<div className={styles.chatMessageStream} style={{ marginBottom: isLoading ? "40px" : "0px" }}>
							{chatHistory.map((chat, index) => (
								<>
									{chat.type === "question" ? (
										<div className={styles.chatMessageUser}>
											<div className={styles.chatMessageUserMessage} style={{ background: "#2c66a1", color: "white" }}>
												{chat.message}
											</div>
										</div>
									) : chat.type === "answer" ? (
										<div className={styles.chatMessageGpt}>
											<div className={styles.chatMessageUserMessage}>{chat.message}</div>
										</div>
									) : null}
								</>
							))}
							{showLoadingMessage && (
								<>
									<div className={styles.chatMessageUser}>
										<div className={styles.chatMessageUserMessage} style={{ background: "#2c66a1", color: "white" }}>
											{lastQuestionRef.current}
										</div>
									</div>
									<div className={styles.chatMessageGpt}>
										<Answer
											answer={{
												answer: "A gerar resposta...",
												citations: [],
											}}
											onCitationClicked={() => null}
										/>
									</div>
								</>
							)}
							<div ref={chatMessageStreamEnd} />
						</div>
					)}

					<Stack horizontal className={styles.chatInput}>
						{/* {isLoading && (
							<Stack horizontal className={styles.stopGeneratingContainer} role="button" aria-label="Parar de gerar" tabIndex={0} onClick={stopGenerating} onKeyDown={(e) => (e.key === "Enter" || e.key === " " ? stopGenerating() : null)}>
								<SquareRegular className={styles.stopGeneratingIcon} aria-hidden="true" />
								<span className={styles.stopGeneratingText} aria-hidden="true">
									Parar de gerar
								</span>
							</Stack>
						)} */}
						{chatHistory.length > 0 && <BroomRegular className={styles.clearChatBroom} style={{ background: "radial-gradient(109.81% 107.82% at 100.1% 90.19%, #0F6CBD 33.63%, #2D87C3 70.31%, #8DDDD8 100%)", cursor: "pointer" }} onClick={clearChat} onKeyDown={(e) => (e.key === "Enter" || e.key === " " ? clearChat() : null)} aria-label="Limpar sessão" role="button" tabIndex={0} />}
						<QuestionInput clearOnSend placeholder="Escreva uma pergunta..." disabled={isLoading} onSend={(question) => makeQuestion(question)} />
					</Stack>
				</div>
				{/* {answers.length > 0 && isCitationPanelOpen && activeCitation && (
					<Stack.Item className={styles.citationPanel}>
						<Stack horizontal className={styles.citationPanelHeaderContainer} horizontalAlign="space-between" verticalAlign="center">
							<span className={styles.citationPanelHeader}>Citações</span>
							<DismissRegular className={styles.citationPanelDismiss} onClick={() => setIsCitationPanelOpen(false)} />
						</Stack>
						<h5 className={styles.citationPanelTitle}>{activeCitation[2]}</h5>
						<ReactMarkdown className={styles.citationPanelContent} children={activeCitation[0]} remarkPlugins={[remarkGfm]} rehypePlugins={[rehypeRaw]} />
					</Stack.Item>
				)} */}
			</Stack>
			<div className={styles.divButtonReferences}>
				{!isReferenciasPanelOpen ? (
					<div className={styles.divButtonReferences} style={{ marginRight: "20px" }}>
						<span
							className={styles.buttonOpenCloseReferences}
							onClick={() => {
								setIsReferenciasPanelOpen(true);
							}}
							aria-label="Abrir Referências"
							style={{ paddingRight: "2px" }}
							role="button">
							&#8249;
						</span>
						<span>Ver Referências</span>
					</div>
				) : (
					<span
						className={styles.buttonOpenCloseReferences}
						onClick={() => {
							setIsReferenciasPanelOpen(false);
						}}
						aria-label="Fechar Referências"
						role="button">
						&#8250;
					</span>
				)}
			</div>
			{isReferenciasPanelOpen && (
				<Stack horizontal className={styles.chatRootReferencias}>
					<span className={styles.labelReferenciasTitle}>Documentos de Referência:</span>
					<div className={styles.chatContainerReferencia}>
						<div className={styles.chatMessageStream}>
							{referencias.map((referencia, index) => (
								<>
									{
										<div className={styles.chatMessageUser} key={index}>
											<div className={styles.chatMessageUserMessageRef} key={index + 1}>
												{referencia}
											</div>
										</div>
									}
								</>
							))}
						</div>
					</div>
					{questions.length > 0 && (
						<div className={styles.chatContainerNumeroMensagens}>
							<span className={styles.numeroMensagens}>Mensagem: {questions.length}/5</span>
						</div>
					)}
				</Stack>
			)}
		</div>
	);
};

export default Chat;
